<template>
    <div>
        
        <Nav showUser=ture />
        <div class="banner">
            <img  id="slide1" src="@/yo_0512/home_slide1.svg">
            <img  id="slide2" src="@/yo_0512/home_slide2.svg">
        </div>
        <div class="title">AURA</div>
        <div class="text_nofade">
            Aura
        </div>
        

        <!--1-->
        <div style="height: 250px;width:375px;position:relative;overflow:hidden">
            <div class="circle" id="c1" ref="c1"></div>
            <div id="c1_img" ref="c1_img"><img   src="@/assets/svg/home_c1_img.svg"></div>
        </div>
        <div class="title2" ref="c1_title"></div>
        <div class="text" ref="c1_text">
            Aura 
        </div>

        <!--2-->
        <div style="height: 250px;width:375px;position:relative;overflow:hidden">
            <div class="circle" id="c2" ref="c2"></div>
            <div id="c2_img" ref="c2_img"><img   src="@/assets/svg/home_c2_img.svg"></div>
        </div>
        <div class="title2" ref="c2_title"></div>
        <div class="text" ref="c2_text">
            Aura Aura 
        </div>

        <!--3-->
        <div style="height: 250px;width:375px;position:relative;overflow:hidden">
            <div class="circle" id="c3" ref="c3"></div>
            <div id="c3_img" ref="c3_img"><img   src="@/assets/svg/home_c3_img.svg"></div>
        </div>
        <div class="title2" ref="c3_title"></div>
        <div class="text" ref="c3_text">
            Aura
        </div>

        <div class="bottom">
            70113<br>
            (06)275757550320(06)2766421<br>
            2021 AURA |  <br>
        </div>



        <Footer showHome=true />
    </div>
    
</template>
<style scoped>
#slide1{
    position: absolute;
    /* animation  */
    animation-name:slidemove_1;    /* keyframe */
    animation-duration: 7s;    /**/
    animation-delay: 0s;    /**/
    animation-iteration-count: infinite;    /*infinite */ 

}
#slide2{
    position: absolute;
    right: -375px;
    /* animation  */
    animation-name:slidemove_2;    /* keyframe */
    animation-duration: 7s;    /**/
    animation-delay: 0s;    /**/
    animation-iteration-count: infinite;    /*infinite */ 

}

@keyframes slidemove_1{
    /*  */
    0% { transform: translateX(0px);}
    40% { transform: translateX(0px);}

    /* out */
    40% { transform: translateX(0px);}
    50% {transform: translateX(-375px); opacity: 1;}

    /* slide2 */
    51% {transform: translateX(-375px);opacity: 0;}
    52% {transform: translateX(375px);opacity: 0;}
    53% {transform: translateX(375px);opacity: 1;}

    /* in */
    90% {transform: translateX(375px);opacity: 1;}
    100% {transform: translateX(0px); }
}
@keyframes slidemove_2{
    /*  */
    0% { transform: translateX(0px);opacity: 0;}
    40% { transform: translateX(0px);opacity: 1;}

    /* in */
    40% { transform: translateX(0px);}
    50% {transform: translateX(-375px); opacity: 1;}

    /*  */
    50% {transform: translateX(-375px); opacity: 1;}
    90% {transform: translateX(-375px); opacity: 1;}
    

    /* out */
    
    100% {transform: translateX(-750px);opacity: 0; }
    
}
.bottom{
    margin-top: 60px;
    padding-top: 16px;
    width: auto;
    height: 90px;
    /* 5 */

    font-family: Taipei Sans TC Beta;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;


    text-align: center;

    color: #FFFFFF;

    /*  */

    background: #1E1E1E;
    box-shadow: 0px -0.5px 0px rgba(0, 0, 0, 0.3);
}
#c1{
    position:absolute;
    left: -168px;
    top:50px;
    

}
#c1_img{
    position:absolute;
    right: -188px;
    top:80px;
    
}
#c2{
    position:absolute;
    right: -168px;
    top:50px;
    

}
#c2_img{
    position:absolute;
    left: -188px;
    top:80px;
    
}
#c3{
    position:absolute;
    left: -168px;
    top:50px;
    

}
#c3_img{
    position:absolute;
    right: -188px;
    top:80px;
    
}
.circle{
    height: 168px;
    width: 168px;
    border-radius: 50%;
    background: linear-gradient(180deg, rgba(150, 251, 196, 0.7) 33.15%, rgba(32, 226, 215, 0.7) 127.81%);

}
.leftin{
    /* animation  */
    animation-name:leftin;    /* keyframe */
    animation-duration: 1.5s;    /**/
    animation-delay: 0s;    /**/
    animation-iteration-count: 1;    /*infinite */ 
    animation-fill-mode :forwards;
}
@keyframes leftin{
    0% { opacity: 0;transform: translateX(0px);}
    50% { opacity: 1;transform: translateX(250px);}
    100% {opacity: 1;transform: translateX(250px); }
}
.rightin{
    /* animation  */
    animation-name:rightin;    /* keyframe */
    animation-duration: 1.5s;    /**/
    animation-delay: 0s;    /**/
    animation-iteration-count: 1;    /*infinite */ 
    animation-fill-mode :forwards;
}
@keyframes rightin{
    0% { opacity: 0;transform: translateX(0px);}
    50% { opacity: 1;transform: translateX(-250px);}
    100% {opacity: 1;transform: translateX(-250px); }
}
.text_fadein{
    /* animation  */
    animation-name:text_fadein;    /* keyframe */
    animation-duration: 1s;    /**/
    animation-delay: 0.3s;    /**/
    animation-iteration-count: 1;    /*infinite */ 
    animation-fill-mode :forwards;
}
@keyframes text_fadein{
    0% { opacity: 0;}
    100% {opacity: 1;}
}
.title2{
    font-family: Taipei Sans TC Beta;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;

    text-align: center;

    color: #4F4F4F;
    opacity: 0;
}
.text{
    font-family: Taipei Sans TC Beta;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    text-align: left;
    margin-top: 15px;
    padding-left: 8vw;
    padding-right: 8vw;

    /*  */

    color: #5C5C5C;
    opacity: 0;
}
.text_nofade{
    font-family: Taipei Sans TC Beta;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    text-align: left;
    margin-top: 15px;
    padding-left: 8vw;
    padding-right: 8vw;

    /*  */

    color: #5C5C5C;

}
.title{
    font-family: Taipei Sans TC Beta;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    margin-top: 45px;

    color: #4F4F4F;
}
.banner{
    position: relative;
    width: 375px;
    height: 220px;
    overflow: hidden;

    background: #EFFFFD;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.1);
}
</style>
<script>
import Nav from '@/components/Nav.vue'
import Footer from '@/components/Footer.vue'

export default {
    name: 'Homepage',
    data(){
        return{

        }
    },
    created () {
        console.log('== created ==')
        window.addEventListener('scroll', this.handleScroll);
        localStorage.clear();
        localStorage.setItem("isWater","F");
    },
    destroyed () {
        console.log('== destroyed ==')
        window.removeEventListener('scroll', this.handleScroll);
    },
    
    methods: {
        handleScroll () {
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            console.log(scrollTop)
            if(scrollTop>=50){
                this.$refs.c1.classList.add("leftin");
                this.$refs.c1_img.classList.add("rightin");
                this.$refs.c1_text.classList.add("text_fadein");
                this.$refs.c1_title.classList.add("text_fadein");
            }
            if(scrollTop>=400){
                this.$refs.c2.classList.add("rightin");
                this.$refs.c2_img.classList.add("leftin");
                this.$refs.c2_text.classList.add("text_fadein");
                this.$refs.c2_title.classList.add("text_fadein");
            }
            if(scrollTop>700){
                this.$refs.c3.classList.add("leftin");
                this.$refs.c3_img.classList.add("rightin");
                this.$refs.c3_text.classList.add("text_fadein");
                this.$refs.c3_title.classList.add("text_fadein");
            }
            
        },
    },
    components:{
        Nav,
        Footer
    }
}
</script>


