import Vue from 'vue'
import Router from 'vue-router'
import profile from '@/views/profile.vue'
import reservation from '@/views/reservation.vue'
import Picktime from '@/views/Picktime.vue'
import Matchresult from '@/views/Matchresult.vue'
import Reservationsuccess from '@/views/Reservationsuccess.vue'
import Feeling from '@/views/Feeling.vue'
import Temp from '@/views/Temp.vue'
import DiaryMain from '@/views/DiaryMain.vue'
import DiaryWrite from '@/views/DiaryWrite.vue'
import Home from '@/views/Homepage.vue'
import reserve from '@/views/reserve.vue'
import start from '@/views/start.vue'
import analysis from '@/views/analysis.vue'
import Otherresource from '@/views/Otherresource.vue'

/*  */
import Press from '@/yo_0512/Press.vue'
import PressQ1 from '@/yo_0512/PressQ1.vue'

Vue.use(Router)

export default new Router({
  //  mode: 'history'  HTML5 history  # 
  mode: 'history',
  routes: [
    {
      path: '/profile',
      component: profile
    },
    {
      path: '/reservation',
      component: reservation
    },
    {
      path: '/Matchresult',
      name: 'Matchresult',
      component: Matchresult
    },
    {
      path: '/Picktime',
      name: 'Picktime',
      component: Picktime
    },
    {
      path: '/Reservationsuccess',
      name: 'Reservationsuccess',
      component: Reservationsuccess
    },
    {
      path: '/temp',
      component: Temp
    },
    {
      path: '/feeling',
      component: Feeling
    },
    {
      path: '/diary',
      component: DiaryMain
    },
    {
      path: '/diary/write',
      component: DiaryWrite
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/reserve',
      component: reserve
    },
    {
      path: '/start',
      component: start
    },
    {
      path: '/analysis',
      component: analysis
    },
    {
      path: '/Otherresource',
      name: 'Otherresource',
      component: Otherresource
    },
    {
      path: '/press',
      name: 'Press',
      component: Press
    },
    {
      path: '/press/q1',
      name: 'PressQ1',
      component: PressQ1
    }
  ]
})



