				</blockquote>
				
				<blockquote>
					<div class="content_list">
						<li></li>
						<p3> </p3>
					</div>
				</blockquote>
				
				<blockquote>
					<div class="content_list">
						<li></li>
						<p3></p3>
					</div>
				</blockquote>
							
