	padding: 0;
	margin: 0;
}

button{
	border: none;
    outline: none;
    background: none;
    padding: 0;
    border-radius: 1vw;
    cursor: pointer;
}

blockquote{
	margin-block: 0;
}

.button_font{
	/*font-family: Taipei Sans TC Beta;*/
	font-size: 2.22vh;
	font-weight: normal;
	color: #FFFFFF;
}

.full_img{
	height: 100%;
}

.full_width_img{
	width: 100%;
}

#layout_up{
	display: flex;
	flex-direction: column;
    align-items: center;
}

#navbar{
	width: 100vw;
	height: 6.94vh;
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	z-index: 10;
	
	background: #FFFFFF;
	box-shadow: 0 0.28vh 1.39vh rgba(118, 156, 145, 0.25);
}

#navbar_main{
	width: 11.39vh;
	height: 2.54vh;
}

#navbar_side{
	display: flex;
    justify-content: space-around;
    width: 15vw;
}

.navbar_button{
	width: 3.89vh;
	height: 3.89vh;
}

#navbar_record{
