	z-index: -1;
}

#famous_box_left{
	width: 40%;
	height: 100%;
}

#famous_box_right{
	width: 60%;
	height: 100%;
	
	display: flex;
	flex-direction: column;
    justify-content: center;
    align-items: flex-end;
	
	font-weight: 400;
	color: #5C5C5C;
}

#famous_box_pic{
