	font-weight: 400;
	line-height: 4.72vh;

	color: #20E2D7;
}

t2{
	font-size: 1.8vh;
	font-weight: 400;
	line-height: 5vh;
}

t3{
	font-size: 1.4vh;
	font-weight: 400;
	line-height: 5vh;
}

.content_list{
	display: flex;
    align-items: baseline;
}

li{
	font-size: 1.4vh;
	line-height: 2.5vh;
}

p3{
	font-size: 1.4vh;
	font-weight: 300;
	line-height: 2.5vh;
	display: flex;
}

#accept_button{
	position: relative;
    top: 10vh;
    display: flex;
	flex-direction: row;
	align-items: center;
    justify-content: center;
	
	background: #20E2D7;
	box-shadow: 0 0.55vh 2.36vh -0.14vh rgba(107, 182, 177, 0.51);
	border-radius: 4.5vh;
	
	height: 6vh;
    
	cursor: pointer;
}

#base{
	position: relative;
    top: 15vh;
    height: 3vh;
}
</style>



